import { Component, Input, OnInit } from '@angular/core';
import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
@Component({
  selector: 'app-template-sub-pos2',
  templateUrl: './template-sub-pos2.component.html',
  styleUrls: ['./template-sub-pos2.component.scss']
})
export class TemplateSubPos2Component implements OnInit {
  @Input() invoice: any;
  @Input() invoice_detail:any;
  @Input() subTotal:any;
  constructor(
    public vhAlgorithm: VhAlgorithm,
    public vhAuth: VhAuth
  ) {
  }

  ngOnInit(): void {}
 

  getNumbersProduct() {
    return this.invoice_detail.reduce((sum: number, next) => sum + next.quantity, 0)
  }
}
