import { VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-template-sub-pos1',
  templateUrl: './template-sub-pos1.component.html',
  styleUrls: ['./template-sub-pos1.component.scss']
})
export class TemplateSubPos1Component implements OnInit {
  @Input() invoice:any;
  @Input() invoice_detail:any;
  @Input() subTotal:any;
  constructor(
    public vhAlgorithm:VhAlgorithm,
    public vhAuth:VhAuth
  ) { }

  ngOnInit(): void {
  }

  getNumbersProduct() {
    return this.invoice_detail.reduce((sum: number, next) => sum + next.quantity, 0)
  }
}
