
import { Component, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhSaleInvoiceEdit } from 'src/app/sales/components/bill-type/bill-type-1/sales-desktop/sales-desktop-interface';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-customer-pos',
  templateUrl: './customer-pos.component.html',
  styleUrls: ['./customer-pos.component.scss']
})
export class CustomerPosComponent {
  @Input() visibleCustomer: boolean = false;
  @Output() hideVisibleCustomer: EventEmitter<boolean> = new EventEmitter()
  // i tng ha n
  @Input() invoices: VhSaleInvoiceEdit;
  // ID khch hng
  @Input() customerID: string;
  @Output() eventChangeCustomerType: EventEmitter<boolean> = new EventEmitter()
  @Output() eventChangeCustomer: EventEmitter<any> = new EventEmitter()
  // Cp nht ha n khi c s thay i
  @Output() updateInvoice: EventEmitter<any> = new EventEmitter()
  @Output() changeDebt: EventEmitter<boolean> = new EventEmitter() // trng thi cho php n b thay i
  customerList: any[] = []
  customerSearchList: any[] = []
  customerType: 1 | 2;
  // Customer
  showDrawerAddCustomer: boolean = false
  showDrawerCustomerInfo: boolean = false
  show_box_search: boolean = false;
  customer;
  /** im tch ly cng thm */
  @Input() earning_point: number;
  /** im tch ly hin ti */
  point_validity: number = 0;



  constructor(
    public vhQuerySales: VhQuerySales,
    private vhAlgorithm: VhAlgorithm,
    private nzModalService: NzModalService,
    private vhcomponent: VhComponent,
    private lang: LanguageService
  ) { }
  


  initCustomer() {
    this.customerList = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item._id != 'id_retail');
    this.customerSearchList = this.customerList;
    if (this.customerID == "id_retail") {
      this.customerType = 1
    } else {
      this.customerType = 2
    }
    this.customer = this.vhQuerySales.getlocalCustomer(this.customerID)
    this.point_validity = this.vhQuerySales.getlocalCustomerPoints(this.customerID)
  }



  ngOnChanges({ customerID }: SimpleChanges) {
    if (customerID)
      if (customerID.currentValue) {
        this.initCustomer()
      }
  }



  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }



  /**
   * thay i loi khch hng 
   * 1 : khch l - id_customer : id_retail
   * 2 :member 
   */
  changeCustomerType(type) {
    console.log(type);

    if (type == 1) {
      this.show_box_search = false
      this.customerID = "id_retail"
      this.invoices.id_customer = "id_retail";
      this.invoices.tax_buyerDisplayName = 'Ngi mua khng ly ho n';
      this.invoices.tax_buyerLegalName = '';
      this.invoices.tax_buyerTaxCode = '';
      this.invoices.tax_buyerAddressLine = '';
      this.invoices.tax_buyerEmail = '';
      this.changeDebt.emit(false);
    } 
    else if (type == 2 && !this.invoices.retail_name) {
      this.customer = '';
      this.customerList = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item._id != 'id_retail');
      this.customerSearchList = this.customerList
      setTimeout(() => document.getElementById("select-customer").click(), 200)
    }

    if (type == 2 && this.invoices.retail_name) {
      this.nzModalService.confirm({
        nzTitle: `${this.lang.translate('Retail customer information will be lost')}?`,
        nzCancelText: this.lang.translate("Cancel"),
        nzOkText: "Ok",
        nzOnOk: () => {
          this.clearRetailCustomer();
          this.customer = '';
          this.customerList = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item._id != 'id_retail');
          this.customerSearchList = this.customerList
          setTimeout(() => document.getElementById("select-customer").click(), 200)
        },
        nzOnCancel: () => { this.customerType = 1 },
      })
    }
  }



  blurCustomer() {
    setTimeout(() => {
      if (this.customerID == "id_retail") this.customerType = 1;
    }, 500)
  }



  /**
   * Khi thay i khch hng
   * @param id_customer ID khch hng c chn
   */
  changeCustomer(id_customer) {
    this.invoices.id_customer = id_customer;
    const localCustomer = this.vhQuerySales.getlocalCustomer(id_customer);
    this.invoices.tax_buyerDisplayName = localCustomer?.name || '';
    this.invoices.tax_buyerLegalName = localCustomer?.name || '';
    this.invoices.tax_buyerTaxCode = localCustomer?.taxcode || '';
    this.invoices.tax_buyerAddressLine = localCustomer?.address || '';
    this.invoices.tax_buyerEmail = localCustomer?.email || '';

    if (this.invoices.retail_name) this.clearRetailCustomer();
    let debt = this.vhQuerySales.getlocalCustomer(id_customer).debt_enable;
    this.changeDebt.emit(debt ? debt : false);
    this.eventChangeCustomer.emit(id_customer);
  }



  clearRetailCustomer() {
    this.invoices.retail_address = ('')
    this.invoices.retail_name = ('')
    this.invoices.retail_phone = ('')
  }



  // Customer process
  openAddCustomer() {
    this.showDrawerAddCustomer = true
  }



  openCustomerInfo() {
    this.showDrawerCustomerInfo = true
  }



  closeCustomerInfo(ev) {
    this.showDrawerCustomerInfo = false
    if (ev) this.customerList = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item._id != 'id_retail');
  }



  searchCustomer(event) {
    this.customerList = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(event.toString().toLowerCase()), this.customerSearchList, ['name', 'phone'])
  }



  /**add customer */
  showDrawerAddRetail: boolean = false
  openDrawerAddCustomerOrRetail() {
    if (this.customerType == 1) this.showDrawerAddRetail = true
    else this.showDrawerAddCustomer = true
  }



  closeAddCustomer(result) {
    if (this.customerType == 1) {
      if (result) {
        this.invoices.retail_name = (result.retail_name)
        this.invoices.retail_phone = (result.retail_phone)
        this.invoices.retail_address = (result.retail_address)
        this.vhQuerySales.updateBill(this.invoices._id, { id_customer: 'id_retail', retail_name: this.invoices.retail_name, retail_phone: this.invoices.retail_phone, retail_address: this.invoices.retail_address })
      }
      this.showDrawerAddCustomer = false
      this.showDrawerAddRetail = false
    }
    else {
      this.showDrawerAddCustomer = false;
      this.customerSearchList.push(result); // thm vo mng
    }
  }



  searchList = []

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length >= 6) {
      console.log('searchKeyword_NamePhone', tempVal);

      this.vhQuerySales.searchKeyword_NamePhone('customers', tempVal).then((res: any) => {
        this.searchList = res.map(item => {
          return { ...item, earning_point: item.earning_point ? item.earning_point : 0 }
        })
        if (this.searchList.length == 0) {
          this.vhcomponent.alertMessageDesktop("warning", this.lang.translate('khong_tim_thay_khach_hang'))
        }
        if (this.searchList.length == 1) {
          this.customerID = this.searchList[0]._id;
          this.changeCustomer(this.customerID)
        }
      })
    }
  }

}
