import { VhQuerySales } from 'ionic-vhframeworks';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-delivery-pos',
  templateUrl: './delivery-pos.component.html',
  styleUrls: ['./delivery-pos.component.scss']
})
export class DeliveryPosComponent implements OnInit {

  @Input() delivery: boolean = false;
  @Output() hideDelivery = new EventEmitter<any>();
  @Input() id_delivery: string = '';
  @Input() id_customer: string = '';
  visibleCheckFees: boolean = false
  listDeli = this.vhQuerySales.getlocalDeliverys().filter(i => i.active).map(item => { return { ...item, img: '../../../assets/img/' + item.type + '.svg' } })
  constructor(
    private vhQuerySales: VhQuerySales
  ) { }

  ngOnInit(): void {
    this.vhQuerySales.refreshLocalDeliverys().then(() => {
      this.listDeli = this.vhQuerySales.getlocalDeliverys().filter(i => i.active).map(item => { return { ...item, img: '../../../assets/img/' + item.type + '.svg' } })
      
    })
  }

  selectDelivery(id) {
    this.id_delivery = this.id_delivery == id ? '' : id;
  }

  handleDeliCancel() {
    this.hideDelivery.emit({ hide: false })
  }

  handleDeliOk() {
    this.hideDelivery.emit({ hide: false, id_delivery: this.id_delivery, update: true })
  }
}
