  /**
   * s kin nhn ouput t app-tax
   */
  closeAndGetValuePayment(event) {

    if (event[0] == 1 || event[0] == 2) {
      this.setPaymentType(event[0]);
      this.invoice_payment = this.invoice.payment.toString();
      this.invoice_cash = this.invoice.cash.toString();
    }
    else if (event[0] == 3) {
      this.setPaymentType(event[0], event[1]);
      this.invoice_payment = this.invoice.payment.toString();
      this.invoice_cash = this.invoice.cash.toString();
    }
  }

  /**
   * cp nht payment_type cho bill
   * @param type
   * @param id_wallet
   * @example  this.setPaymentType(1)
   */
  setPaymentType(type: 1 | 2 | 3, id_wallet?) {
    this.invoice.payment_type = (type)
    switch (type) {
      case 1: {
        this.invoice.id_wallet = ('id_cash'); this.invoice.payment = (this.invoice.payment ? this.invoice.payment : this.getTotalATax());
        this.updateBill()
        break;
      }
      case 2: {
        this.invoice.payment = (0); this.invoice.id_wallet = (null);
        this.invoice.cash = (0);
        this.updateBill()
        break;
      }
      case 3: {
        if (id_wallet) {
          this.invoice.id_wallet = (id_wallet)
          this.invoice.payment = (this.invoice.payment ? this.invoice.payment : this.getTotalATax());
          this.updateBill();
          break;
        }
      }
    };
  }

  /**
   * bt sk nhn nt thanh ton ha n
   * @param print in hay ko
   */
  payInvoice(print?) {
    if (this.vhAuth.checkMyPermission("sales_enable_payment")) {
      if (this.getTotalEarningPoint() || this.invoice.use_points) {
        let earned_points = this.getTotalEarningPoint() - this.value_discount_point
        this.vhQuerySales.updateCustomer_byEarnedPoints(this.invoice.id_customer, earned_points)
        this.invoice.earning_points_bill = (this.getTotalEarningPoint())
      }
      if (this.product_codes.length) {
        let promise = new Array();
        for (let i = 0; i < this.product_codes.length; i++) {
          promise[i] = this.vhQuerySales.updateCouponCode(this.product_codes[i]._id, { status: 4, id_bill: this.invoice._id, date_used: new Date() })
        }
        this.vhQuery.asyncPromiseAll(promise)
          .then((array) => {


            this.payment(print);
          }, error => { })
      }
      else
        this.payment(print);
    }
    else
      this.mess.error(this.languageService.translate('You do not have this rights'))
  }

  cqt_invoice_auto_sign: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_invoice_auto_sign
  cqt_submit_invoice_immediately: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_submit_invoice_immediately

  handleMinvoice(bill) {
    console.log(bill);
    bill = { ...this.getCreateUpdateInvoicePosInterface(1) }

    return new Promise((resolve, reject) => {
      if (!this.cqt_submit_invoice_immediately) {
        resolve(true)
        return
      }
      // ly ra chi nhnh hin ti
      let currentBranch = this.vhQuerySales.getlocalBranch(this.vhQuerySales.getDefaultBranch()._id);
      if (!currentBranch.einvoices) {
        resolve(true)
        return
      }
      // trng hp cha c to kt ni ha n in t
      if (!currentBranch.einvoices?.length) {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
        resolve(true)
        return
      }
      // trng hp  gi ln cqt ri th khi
      if(bill.cqt_status == 2 || bill.cqt_status == 3){
        resolve(true)
        return
      }
      // trng hp ch c 1 kt ni ha n in t th to thng lun
      if (currentBranch.einvoices.length && currentBranch.einvoices.length == 1) {
        if (!currentBranch.einvoices[0].invoiceSeries.length) {
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
          resolve(true)
          return
        }
        this.billService.changeBill_byLocal_toInvoice_byMinvoice(bill._id, currentBranch.einvoices[0].invoiceSeries[0])
          .then((rsp) => {
            console.log('changeBill_toInvoice_byMinvoice', rsp)
            if (rsp.vcode == 0) {
              if (this.cqt_invoice_auto_sign) {
                this.vhEinvoice.saveInvoice_Sign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log('saveInvoice_Sign_byMinvoice', res);
                    if (res.code == '00') {
                      bill.cqt_status = 3
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 3 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {

                      bill.cqt_status = 1
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {

                      bill.cqt_status = 1
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
              else {
                this.vhEinvoice.saveInvoice_noSign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log(res);
                    if (res.code == '00') {
                      bill.cqt_status = 2
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 2 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {

                      bill.cqt_status = 1
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {

                      bill.cqt_status = 1
                      this.vhQuerySales.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
            }
          }, (error: any) => {
            console.log('error', error)
            resolve(true)
          })
      }
    })
  }
