  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.tempDetail.quantity = this.editQuantity ? parseFloat(parseFloat(this.editQuantity).toFixed(2)) : 1;
      this.isVisibleEditUnitPrice = false;
      this.setSubTotal()
      this.updateBill_Detail(this.tempDetail._id, this.getCleanBillDetail(this.tempDetail))
      if (!this.gotten_gift_type7) this.handlePromotionType7(this.bill_details_pmtype_7)
    } else {
      this.mess.error(this.languageService.translate("Invalid data, please check again"))
    }
  }

