  /**
   * tnh li gi bn ca sp sau khi nhp gi tr gim v loi gim gi
   * @example this.caculateDiscountPrice_billDetail()
   */
  caculateDiscountPrice_billDetail() {
    switch (this.default_discount_price_type) {
      case '%': {
        if (this.unit_price_discount.getRawValue() > 100 || this.unit_price_discount.getRawValue() < 0) {
          this.tempDetail.price = 0;
          this.tempDetail.percent_discount_product = 100;
        }
        else {
          this.tempDetail.price = this.tempDetail.price_origin * (1 - this.unit_price_discount.getRawValue() / 100)
          this.tempDetail.percent_discount_product = this.unit_price_discount.getRawValue();
        }
        break;
      }
      case this.symbol: {
        if (this.unit_price_discount.getRawValue() > this.tempDetail.price_origin || this.unit_price_discount.getRawValue() < 0) {
          this.tempDetail.price = 0;
          this.tempDetail.percent_discount_product = 100;
        }
        else {
          this.tempDetail.price = this.tempDetail.price_origin - this.unit_price_discount.getRawValue();
          this.tempDetail.percent_discount_product = parseFloat(parseFloat(((1 - (this.tempDetail.price / this.tempDetail.price_origin)) * 100).toString()).toFixed(2));
        }
        break;
      }
    }
  }

