import { Component, Input, OnInit } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-template-sub-pos3',
  templateUrl: './template-sub-pos3.component.html',
  styleUrls: ['./template-sub-pos3.component.scss']
})
export class TemplateSubPos3Component implements OnInit {
  @Input() invoice: any;
  @Input() invoice_detail:any;
  @Input() subTotal:any;
  constructor(
    public vhAlgorithm: VhAlgorithm,
    public vhQuerySales: VhQuerySales,
    public vhAuth: VhAuth,
  ) {
  }

  ngOnInit(): void {
  }
  getNumbersProduct() {
    return this.invoice_detail.reduce((sum: number, next) => sum + next.quantity, 0)
  }

}
