import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-stock-pos',
  templateUrl: './stock-pos.component.html',
  styleUrls: ['./stock-pos.component.scss']
})
export class StockPosComponent implements OnInit {

  @Input() stock: boolean;
  @Output() closeStock: EventEmitter<any> = new EventEmitter()
  keySearch: any
  // list cha bill
  products: any = []
  segment_product: any = [];
  show_products: any = []
  //Hm nay 0h- 23h59
  endTime: Date = new Date;
  tabs = ['All', 'Allow sale', 'Not allow sale']
  sum_stock: number = 0;
  total: number = 0;
  // value sort
  selectedIndex: any;
  branchList: Array<any> = []
  id_branch_report: string = ''
  useBranch: boolean = this.fncService.checkUsingBranch();
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    public router: Router,
    private cdRef: ChangeDetectorRef,
    public languageService: LanguageService,
    private vhAuth: VhAuth,
    private fncService: FunctionService
  ) {
    this.getBranches()
  }

  ngOnInit() {

    this.keySearch = ''
    this.endTime.setHours(23, 59, 59)
    this.selectedIndex = 0

  }
  getBranches() {
    let branches = [...this.vhQuerySales.getlocalBranchs()]
    let employee = this.vhAuth.getUser()
    if (this.useBranch && employee['owner'] == 'boss') {
      this.branchList = branches
      if (this.branchList.length > 1) this.branchList.unshift({ _id: "all", name: "All" })
    } else if (this.useBranch && employee['owner'] != 'boss') {
      this.vhAuth.getPermission(employee['_id']).then(result => {
        branches.forEach(item => {
          if (result[`${item._id}_view_report_branch`]) this.branchList.push({ ...item })
        })
        if (branches.length == this.branchList.length && this.branchList.length > 1) this.branchList.unshift({ _id: "all", name: "All" })
        else if (this.branchList.length == 1) this.id_branch_report = this.branchList[0]._id
      })
    } else this.id_branch_report = branches[0]._id
  }
  heightScroll: string = '220px';
  getData(endTime) {
    if (this.vhAuth.checkMyPermission('management_enable_stock'))
      this.vhComponent.showLoading("", "current-loading").then(() => {
        this.vhQuerySales
          .getReportInputOutputInventory_byEndTime_byIDProduct(endTime, "all", this.id_branch_report)
          .then((res: any) => {
            this.products = res.docs.map(item => {
              return { id: item['_id'], cost_stock_last_atax: item['cost_stock_last_atax'], stock_last: item['stock_last'], ...item }
            });
            this.segmentChanged(this.selectedIndex);
            this.vhComponent.hideLoading()
          })
      })
    else this.vhComponent.alertMessageDesktop('error', this.languageService.translate("You do not have this rights"),5000)
  }

  searchProduct(value) {
    this.keySearch = value
    this.show_products = this.vhAlgorithm.searchList(
      this.vhAlgorithm.changeAlias(value).toLowerCase(),
      this.segment_product,
      ["name", "barcode"]
    );
  }

  goToDetail(item) {
    this.router.navigate(['/sales/dashboard/manage/stock/warehouse/detail'], {
      state: {
        endTime: this.endTime, ...item,
        dataRestore: {
          keySearch: this.keySearch,
          selectedIndex: this.selectedIndex,
          endTime: this.endTime,
          id_branch_report: this.id_branch_report,
          products: this.products
        }
      }
    })
  }
  setEndTime(value) {
    this.endTime = new Date(value);
    this.endTime.setHours(23, 59, 59);
    this.total = 0;
    this.sum_stock = 0;
  }
  segmentChanged(value) {
    this.selectedIndex = parseInt(value);
    if (this.products.length) {
      this.total = 0;
      this.sum_stock = 0
      switch (parseInt(value)) {
        case 0:
          this.segment_product = this.products.filter(item => {
            this.sum_stock += item.stock_last;
            this.total += item.cost_stock_last_atax;
            return true;
          });
          break;
        
      }
      this.searchProduct(this.keySearch ? this.keySearch : "");
    } else {
      this.show_products = this.products;
    }
  }

  handleCancel() {
    this.closeStock.emit(false)
  }

}
