import { NzNotificationService } from 'ng-zorro-antd/notification';
import { NzModalService } from 'ng-zorro-antd/modal';
import { FunctionService } from 'src/app/sales/services/function.service';
import { TranslateService } from '@ngx-translate/core';

import { Component, OnInit, NgZone } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhAuth, VhQuery, VhQuerySales, VhAlgorithm, VhDisplayLEDService } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Platform } from '@ionic/angular';
import { VhSaleInvoiceEdit } from '../components/bill-type/bill-type-1/sales-desktop/sales-desktop-interface';
import { HttpClient } from '@angular/common/http';
import { ThemeService } from '../services/theme.service';

@Component({
  selector: 'app-sales-pos',
  templateUrl: './sales-pos.component.html',
  styleUrls: ['./sales-pos.component.scss']
})
export class SalesPosComponent implements OnInit {
  useBranch: boolean = this.functionService.checkUsingBranch()
  listInvoices: any[] = [];
  selectedIndex: number = 0
  startTime: Date = new Date()
  endTime: Date = new Date();
  isloaded = false;
  isAdding = false // bin check s kin ang to n
  public langList: any[] = [
    { name: 'Vietnamese', value: 'vi', img: 'assets/icon/settings/language/vi.svg', country_code: 'VN' },
    { name: 'English', value: 'en', img: 'assets/icon/settings/language/en.svg', country_code: 'US' }
  ];
  showModalSelectOrderInterface = false;
  public lang: string = this.vhAuth.localStorageGET('language_code');
  showConfig = false //  bin show modal cu hnh
  showConfigPrint = false//  bin show modal cu hnh may in
  showConfigSales = false//  bin show modal cu hnh trang bn hng
  size: any = [12, 13, 14, 15, 16, 17, 18, 19, 20];
  price_type: any;
  show_modal_choose_interface: boolean = false;
  showPortal = false;
  templates: { name: string; url: string; giai_thich: string; }[] = [];
  store_main_sector = '';
  country_code = 'VN';
  language = 'vi';

  constructor(
    private translateService: TranslateService,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    public vhQuerySales: VhQuerySales,
    private router: Router,
    private vhQuery: VhQuery,
    private mess: NzMessageService,
    private vhAlgorithm: VhAlgorithm,
    private ngZone: NgZone,
    private functionService: FunctionService,
    private nzModalService: NzModalService,
    private notification: NzNotificationService,
    private plaform: Platform,
    private http: HttpClient,
    private vhDisplayLEDService: VhDisplayLEDService,
    public themeService: ThemeService,
  ) {
  }

  ngOnInit(): void {

    this.startTime.setHours(0, 0, 0, 0);
    this.endTime.setHours(23, 59, 59, 59);
    if (!this.vhAuth.localStorageGET("default_discount_price_type")) {
      this.vhAuth.localStorageSET("default_discount_price_type", { value: '%' });
    }
    // if (this.syncStatus) {
    this.useBranch = this.functionService.checkUsingBranch();
    let splash = document.getElementById("lottie-sync")
    if (splash) splash.remove()
    // this.removeSync();
    this.getInvoiceToday();
    this.checkBranches();
    this.user = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id)
    // check xem nv bn mc nh gi no
    if (this.vhAuth.checkMyPermission('default_price_type_1')) this.price_type = 1;
    else if (this.vhAuth.checkMyPermission('default_price_type_2')) this.price_type = 2;

    this.checkExpireAccount();
    if (this.plaform.is('electron')) {
      this.vhQuerySales.connectDisplayLEDScreens()
    }

    this.eventKeypress();

    this.store_main_sector = this.vhAuth.getStore().main_sector; // Ly main_sector ca ca hng
    this.http.get(`assets/documents/configuration/template/${this.country_code}/${this.store_main_sector}.json`).subscribe((templates) => {
      this.templates = templates[this.language];
    }); // Ly giao din theo main_sector ca ca hng
  }

  /**
   * hm ny  cp nht trng default_selling_page dnh cho tk c
   * nu  check permission.desktop_display_page && pos_display_page
   * th cp nht trng default_selling_page = desktop_display_page v permission.selling_display_page = true
   *@example this.updateDefaultSellingPage()
   */
  updateDefaultSellingPage() {
    if (!this.vhAuth.getUser().default_selling_page) { // kim tra nu cha c trng ny trong user th mi tip tc check
      if (this.vhAuth.getUser().owner == 'boss' && !this.vhAuth.getUser().default_selling_page) {
        this.vhAuth.updateEmployee(this.vhAuth.getUser()._id, { default_selling_page: 'sales-desktop' })
      }
      else {
        if (this.vhAuth.checkMyPermission('desktop_display_page') && this.vhAuth.checkMyPermission('pos_display_page'))
          Promise.all([this.vhAuth.updatePermission(this.vhAuth.getUser()._id, { selling_display_page: true }),
          this.vhAuth.updateEmployee(this.vhAuth.getUser()._id, { default_selling_page: 'sales-desktop' })])
        else {
          if (this.vhAuth.checkMyPermission('desktop_display_page')) Promise.all([this.vhAuth.updatePermission(this.vhAuth.getUser()._id, { selling_display_page: true }),
          this.vhAuth.updateEmployee(this.vhAuth.getUser()._id, { default_selling_page: 'sales-desktop' })])
          else if (this.vhAuth.checkMyPermission('pos_display_page')) Promise.all([this.vhAuth.updatePermission(this.vhAuth.getUser()._id, { selling_display_page: true }),
          this.vhAuth.updateEmployee(this.vhAuth.getUser()._id, { default_selling_page: 'sales-pos' })])
        }
      }
    }
  }
  /**
   * kim tra ti khon ht hn cha
   *  @example this.checkExpireAccount()
   */
  checkExpireAccount() {
    // this.vhQuerySales.refreshLocalRestrictionMainPackage().then(()=>{
    let number = this.getNumberOfDay(new Date(), new Date(this.vhQuerySales.getLocalRestrictionMainPackage().expire_date))
    if (number > 3) {// check sau 3 ngy ht hn hin thng bo
      // check tip  cn trong hn c php s dng hay ko
      if (number < this.vhQuerySales.getLocalRestrictionMainPackage().extra_usage_duration) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate('Branch') + ': ' + this.vhQuerySales.getDefaultBranch().name + ' ' + this.languageService.translate('has expired') + '. ' + this.languageService.translate('Please renew to continue') + '!',
          nzOkText: this.languageService.translate('Pay'),
          nzCancelText: "Tip tc",
          nzClosable: false,
          nzCloseIcon: null,
          nzOnOk: () => {
            // go to thanh ton
            this.vhQuerySales.getPayments(
              { id_store: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
              {},
              {},
              0
            ).then((payments: any) => {
              if (payments) {
                if (payments.length > 0) {
                  this.router.navigate(['/sales/payment/detail'],
                    {
                      state: payments
                    });
                } else if (payments == 0) {
                  this.router.navigate(['/sales/payment']);
                }
              }
            })
          },
          nzOnCancel: () => {

          }
        })
      }
      else {
        this.nzModalService.confirm({
          nzTitle: "Chi nhnh hin ti ca bn  tm kha. Vui lng gia hn  tip tc!",
          nzOkText: this.languageService.translate('Pay'),
          nzCancelText: this.languageService.translate('Continue'),
          nzCloseIcon: null,
          nzClosable: false,
          nzOnOk: () => {
            // go to thanh ton
            this.vhQuerySales.getPayments(
              { id_store: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
              {},
              {},
              0
            ).then((payments: any) => {
              if (payments) {
                if (payments.length > 0) {
                  this.router.navigate(['/sales/payment/detail'],
                    {
                      state: payments
                    });
                } else if (payments == 0) {
                  this.router.navigate(['/sales/payment']);
                }
              }
            })
          },
          nzOnCancel: () => {
            this.router.navigate(["/login"])
          }

        })
      }
    }
    // if (number > -7 && number < 0) this.createBasicNotification(number)

    // },
    // err => {
    //   this.router.navigate(["/login"])
    // })
  }
  /**
     * hin th thng bo sp ht hn
     * @param number ngy cn hn
     * @example  this.createBasicNotification(3);
     */
  createBasicNotification(number): void {
    this.notification.blank(
      this.languageService.translate('Your account is about to expire'),
      this.languageService.translate("Your account still has") + " " + parseInt((number * -1).toString()) + " " + this.languageService.translate("days of usage left. Please update your service package to avoid interruptions."),
      {
        nzPlacement: "topRight",
        nzDuration: 8000
      }
    );
  }

  event: any
  /**
   * bt s kin nhn f1-f2-f3-f4-f5
   */
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 115) this.searchProductList = !this.searchProductList;
    }

    document.addEventListener('keydown', this.event, false);
  }

  ngOnDestroy(): void {
    document.removeEventListener('keydown', this.event, false);
    if (this.plaform.is('electron')) this.vhQuerySales.disconnectDisplayLEDScreens()
  }

  /**
   * thit lp li ngn ng hin th trn app
   * @param value bin gi tr language  chn
   * @example this.selectLang('vi')
   */
  public selectLang(value: string) {
    let lang = this.langList.find(item => item.value == value)
    this.vhAuth.localStorageSET('language_code', value)
    this.vhAuth.localStorageSET('country_code', lang.country_code)
    localStorage.setItem("vh-sales-language", value)
    this.translateService.use(value)
    this.languageService.switchLanguage(value)
  }

  /**
   * get ha n trong ngy v gn vo bin listInvoices
   * @example this.getInvoiceToday()
   */
  getInvoiceToday() {
    this.listInvoices = []
    this.vhQuerySales.getBills_byFields({ bill_type: { $eq: 5 }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id }, id_employee: { $eq: this.vhAuth.getUser()._id }, id_shop: { $eq: 'local' } }, {})
      .then((invoice: VhSaleInvoiceEdit[]) => {
        this.listInvoices = invoice.map(item => {
          return { ...item, partner_name: this.renderPartner(item.id_customer), payment_name: this.renderPayment(item.payment_type, item.id_wallet), employee_name: this.vhAuth.getlocalEmployee(item.id_employee).name, phone: this.vhAuth.getlocalEmployee(item.id_employee).phone }
        })
        this.isloaded = true;
        if (!invoice.length) {
          this.newOrder()
        }

      })
  }
  /**
    * tr v tn ca khch hng
    * @param id_partner : id_customer ca bill
    * @example this.renderPartner('JHIDUWHUYDWIUHUDY')
    * @returns string : customer_name | "Not found"
    */
  renderPartner(id_partner: string) {
    let customer: string = this.languageService.translate(this.vhQuerySales.getlocalCustomer(id_partner).name)

    return customer ? customer : this.languageService.translate("Not found")
  }
  /**
    * tr v tn ca phng thc thanh ton
    * @param payment_type : payment_type ca bill
    * @example this.renderPayment(1)
    * @returns string : customer_name | "Not found"
    */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.languageService.translate('Cash')
      case 2: return this.languageService.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }



  /**
   * to bill mi, nhn t sk nt to ha n  tab
   * @example this.newOrder()
   * @returns Promise<bool>
   */
  newOrder(): Promise<any> {
    this.isAdding = true // bin c  check mi ln ch add 1 bill d bm to n nhiu ln cng lc
    // thng tin bill default
    return new Promise((resolve, rejects) => {
      let bill = {
        bill_type: 5,
        payment_type: 1,
        id_customer: 'id_retail',
        tax_buyerDisplayName: 'Ngi mua khng ly ho n',
        tax_buyerLegalName: '',
        tax_buyerTaxCode: '',
        tax_buyerAddressLine: '',
        tax_buyerEmail: '',
        id_shop: 'local',
        id_branch: this.vhQuerySales.getDefaultBranch()._id,
        id_employee: this.vhAuth.getUser()._id,
        id_wallet: 'id_cash',
        date: new Date(),
        cqt_status : 1,
        total: 0,
        payment: 0,
        tax: 0,
        discount: 0,
        fee: 0,
        note: '',
        price_type: 1,
        id_delivery: '',
        payment_points: 0,
        payment_coupons: 0,
        discount_bill: 0,
        earning_points_bill: 0,
        use_points: 0,
        cash: 0
      }
      let default_price_type = 1
      if (this.vhAuth.checkMyPermission('default_price_type_1')) default_price_type = 1;
      else if (this.vhAuth.checkMyPermission('default_price_type_2')) default_price_type = 2;
      bill.price_type = default_price_type
      if (!this.listInvoices.length) { // neu cha c bill th to ri mi push
        this.vhQuerySales.createBill_Billdetail(bill, [])
          .then((bill: any) => {


            this.isAdding = false
            this.listInvoices.push(bill);
            this.selectedIndex = this.listInvoices.length;
            resolve(true)
          }, (error: any) => {

            rejects(error)
          })
      }
      else {// neu  c bill th  push trc ri to mi
        this.listInvoices.push({ ...bill, bill_code: 'New bill', _id: '', id_shop: 'local' });
        Promise.all([
          this.vhQuerySales.createBill_Billdetail(bill, [])])
          .then(([bill]) => {

            this.isAdding = false
            this.listInvoices[this.listInvoices.length - 1]._id = bill._id;
            this.listInvoices[this.listInvoices.length - 1].bill_code = bill.bill_code;
            this.selectedIndex = this.listInvoices.length;
            resolve(true)
          }, (error: any) => {

            rejects(error)
          })
      }
    })
  }
  /**
   * xa bill, nhn t sk xa tab
   * @param index : v tr bill trong mng
   * @example this.closeOrder(1)
   */
  closeOrder({ index }: { index: number }): void {
    // if (this.vhAuth.checkMyPermission('sales_enable_create_bill'))
    this.vhComponent.alertConfirm(`${this.languageService.translate("Confirm")}`, "", `${this.languageService.translate("Are you sure to cancel the sales order")}?`, "OK", this.languageService.translate('Cancel'))
      .then(() => {
        if (index == this.selectedIndex) this.vhQuerySales.closeSyncOpeningBill()
        this.vhComponent.showLoading('').then(() => {
          this.vhQuerySales.deleteBill_Billdetail(this.listInvoices[index]._id)
            .then(() => {
              this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Invoice deleted successfully") + "!")
              this.listInvoices.splice(index, 1);
              if (!this.listInvoices.length) {
                this.newOrder().then(() => {
                  this.vhComponent.hideLoading(0)
                })
              }
              else
                this.vhComponent.hideLoading(0);
            }, (error: any) => {
              this.listInvoices.splice(index, 1);
              if (!this.listInvoices.length) {
                this.newOrder().then(() => {
                  this.vhComponent.hideLoading(0)
                })
              }
              else
                this.vhComponent.hideLoading(0);
              this.mess.create('error', this.languageService.translate("An error while deleting this invoice"))
            })
        })
      }, () => { })
    // else this.mess.create('error', this.languageService.translate("You do not have this rights"),{nzDuration:5000})
  }

  /**
   * xa bill trong local nhn t output tr v bill b error ko th load
   * @param index
   * @example this.deleteBillError(1)
   */
  deleteBillError(index) {


    this.listInvoices.splice(index, 1);
    if (!this.listInvoices.length) {
      this.newOrder()
    }
  }

  /**
   * x l s kin thanh toan bill hoc xa bill
   * @param id_bill
   * @example this.handleSaveBill('GBDUWYGQLHODIUEWDWQF')
   */
  handleSaveBill(id_bill) {
    this.listInvoices.splice(this.listInvoices.findIndex(item => item._id == id_bill), 1);
    if (!this.listInvoices.length) {
      this.newOrder().then(() => {
      })
    }
    // this.selectedIndex = this.listInvoices.length >0 ? --this.selectedIndex : 0;
  }

  //Search product list
  searchProductList: boolean = false


  /**
     * ng xut khi app
     * @example this.logout()
     */
  logout() {
    this.vhAuth.signOut().then(() => {
      this.vhQuerySales.closeSyncCollections();
      this.vhQuerySales.clearDefaultBranch() //xa branchDefault trc khi thot app
      this.router.navigate(["/login"]);
    }, error => {
      console.error('error', error);
    })
  }

  /**
   * chuyn qua trang qun tr v cp nht li default_display_page ca user
   * @param url
   * @param permission
   * @example this.updateInterface('dashbord', 'admin_display_page')
   */
  public updateInterface(type, permission) {
    if (this.vhAuth.checkMyPermission(permission)) {
      Promise.all([this.vhComponent.showLoading(this.languageService.translate("Changing interface..."), "current-loading", null, null, false),
      this.vhAuth.updateMyPermission({ default_display_page: permission }),
      ])
        .then(() => {
          this.vhComponent.hideLoading(0)
          setTimeout(() => {
            this.router.navigate(['/sales/' + type], { state: { isSync: true } });
          }, 200);

        })
    }
    this.vhAuth.localStorageSET("isFirstTime_sales_pos", true)
  }

  default_selling_page = this.vhAuth.getLocalMyPermissionName('default_selling_page')
  /**
     * thit lp giao din trang bn hng mc nh khi m app : sale-pos | sale-desktop
     * @example this.updateSalesInterface()
     */
  updateSalesInterface() {
    this.vhComponent.showLoading('').then(() => {
      this.vhAuth.updateMyPermission({ default_selling_page: this.default_selling_page, default_display_page: 'selling_display_page' })
        .then((bool) => {
          this.vhComponent.hideLoading(0)
            .then(() => {
              this.router.navigate(['/sales/' + this.default_selling_page], { state: { isSync: true } })
            })
        })
    })
  }


  /////////////------------------------FONT_SIZE---------------------------//////////////
  public fontSize: number = parseFloat(localStorage.getItem("vh-sales-system-fontsize"));
  public setFontSize(value) {
    localStorage.setItem("vh-sales-system-fontsize", JSON.stringify(value))
    this.fontSize = value
    document.documentElement.style.fontSize = value + 'px'
  }
  /////////////------------------------COLOR---------------------------//////////////
  changeColor(theme, event) {
    this.vhAuth.changeTheme(theme, event)
  }

  /**X l branches */
  branchList: Array<any> = [];
  branchDefault: any = {}
  /**
   * get danh sch chi nhnh ng nhp ca user
   */
  checkBranches() {
    // this.vhQuerySales.refreshLocalBranchs().then(() => {
    let branches = [...this.vhQuerySales.getlocalBranchs()]
    let employee = this.vhAuth.getUser()
    // check branch latest
    this.branchDefault = this.vhQuerySales.getDefaultBranch()
    let thisBranch = this.vhQuerySales.getlocalBranch(this.vhQuerySales.getDefaultBranch()._id)
    if (this.branchDefault.name != thisBranch.name) {
      this.vhQuerySales.clearDefaultBranch()
      this.vhQuerySales.setDefaultBranch(thisBranch._id)

      this.branchDefault = this.vhQuerySales.getDefaultBranch()
    }
    if (employee['owner'] == 'boss') this.branchList = branches
    else {
      this.vhAuth.getPermission(employee['_id']).then(result => {
        branches.forEach(item => {
          if (result[`${item._id}_login_branch`]) this.branchList.push(item)
        })
      })
    }
    // })
  }

  /**
   * nhn t sk chn thay i chi nhnh  menu header, m popup xc nhn v router qua trang login
   * @example this.change_branch()
   */
  change_branch() {
    this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Are you sure to change branch") + "?", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhQuerySales.clearDefaultBranch();
        this.router.navigate(['/login'], { state: { change_branch: true } })
      }).catch(() => { })
  }
  modalAccountInfo: boolean = false;//show/hide moal thng tin ti khon
  user: any = this.vhAuth.getUser() // thng tin ti khon hin ti
  /**ng modal v get li thng tin user */
  closeAccountInfo(event) {
    this.user = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id)
    this.modalAccountInfo = false;
  }


  /**
   * tr v khong cch gia 2 ngy
   * vd : getNumberOfDay(15/02/2022, 10/02/2022) => return 5
   * */
  getNumberOfDay(date1, date2) {
    return (date1.getTime() - date2.getTime()) / (24 * 3600 * 1000)
  }

  public gotoChangeVoucher() {
    this.router.navigateByUrl('/sales/exchange-voucher')
    this.vhAuth.localStorageSET("isFirstTime_sales_pos", true)
  }

  gotoPrepaidCard() {
    this.router.navigateByUrl('/sales/prepaid-card', { state: { url: this.router.url } })
    this.vhAuth.localStorageSET("isFirstTime_sales_pos", true)
  }
  gotoSalesVoucher() {
    this.router.navigateByUrl('/sales/sales-voucher')
  }

  gotoSalesPrepaidCard() {
    this.router.navigateByUrl('/sales/sales-payment-card')
  }

  gotoSalesOrder() {
    this.router.navigateByUrl('/sales/bill-web-app/sales-order')
  }
  gotoSalesOrderVoucher() {
    this.router.navigateByUrl('/sales/bill-web-app/sales-order-voucher')
  }
  gotoExchangeOrderVoucher() {
    this.router.navigateByUrl('/sales/bill-web-app/exchange-order-voucher')
  }
  gotoSalesOrderPrepaidCard() {
    this.router.navigateByUrl('/sales/bill-web-app/sales-order-payment-card')
  }
  gotoExchangeOrderCard() {
    this.router.navigateByUrl('/sales/bill-web-app/exchange-order-payment-card')
  }
}
