import { AccountInformationComponent } from './components/account-information/account-information.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesPosComponent } from '../sales-pos/sales-pos.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { CreateOrderModule } from './create-order/create-order.module';
import { LottieModule } from 'ngx-lottie';
import { playerFactory } from 'src/app/app.module';
import { ConfigPrintComponent } from './components/config-print/config-print.component';
import { ConfigSalesComponent } from './components/config-sales/config-sales.component';
import { UserGuideComponent } from './user-guide/user-guide.component';
const routes: Routes = [
  {
    path: '',
    component: SalesPosComponent,
  },
  {
    path: 'invoices-pos',
    loadChildren: () => import('./components/invoices-pos/invoices-pos.module').then(m => m.InvoicesPosModule)
  },
]

@NgModule({
  declarations: [
    SalesPosComponent,AccountInformationComponent, ConfigPrintComponent, ConfigSalesComponent,UserGuideComponent
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    CreateOrderModule,
    LottieModule.forRoot({ player: playerFactory }),
    // NgxVirtualKeyboardModule
  ]
})
export class SalesPosModule { }
