import { VhAlgorithm, VhAuth, VhImage, VhQuerySales } from 'ionic-vhframeworks';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-config-sales',
  templateUrl: './config-sales.component.html',
  styleUrls: ['./config-sales.component.scss']
})
export class ConfigSalesComponent implements OnInit {
  best_selling: any = this.vhAuth.localStorageGET("show_best_selling");
  show_confirm_pay : any = this.vhAuth.localStorageGET("show_confirm_pay");
  show_product_mutil_line  : any = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_sales;
  public tax: any = this.vhAuth.localStorageGET("show_tax");
  public fee: any = this.vhAuth.localStorageGET("show_fee");
  public discount: any = this.vhAuth.localStorageGET("show_discount");
  public note: any = this.vhAuth.localStorageGET("show_note");
  show_note_for_each_product: any = this.vhAuth.localStorageGET("show_note_for_each_product");
  public cash: any = this.vhAuth.localStorageGET("show_cash");
  rest: any = this.vhAuth.localStorageGET("show_rest");
  
  style_template_sub_pos: any = this.vhAuth.localStorageGET("style_template_sub_pos");
  show_total_sub_pos_1: any = this.vhAuth.localStorageGET("show_total_sub_pos_1");
  show_fee_sub_pos_1: any = this.vhAuth.localStorageGET("show_fee_sub_pos_1");
  show_discount_sub_pos_1: any = this.vhAuth.localStorageGET("show_discount_sub_pos_1");
  show_pay_sub_pos_1: any = this.vhAuth.localStorageGET("show_pay_sub_pos_1");

  show_total_sub_pos_2: any = this.vhAuth.localStorageGET("show_total_sub_pos_2");
  show_fee_sub_pos_2: any = this.vhAuth.localStorageGET("show_fee_sub_pos_2");
  show_discount_sub_pos_2: any = this.vhAuth.localStorageGET("show_discount_sub_pos_2");
  show_pay_sub_pos_2: any = this.vhAuth.localStorageGET("show_pay_sub_pos_2");


  show_total_sub_pos_3: any = this.vhAuth.localStorageGET("show_total_sub_pos_3");
  show_fee_sub_pos_3: any = this.vhAuth.localStorageGET("show_fee_sub_pos_3");
  show_discount_sub_pos_3: any = this.vhAuth.localStorageGET("show_discount_sub_pos_3");
  show_tax_sub_pos_3: any = this.vhAuth.localStorageGET("show_tax_sub_pos_3");
  show_cash_sub_pos_3: any = this.vhAuth.localStorageGET("show_cash_sub_pos_3");
  image_template_sub_pos2: any = this.vhAuth.localStorageGET("image_template_sub_pos2");
  default_discount_price_type = this.vhAuth.localStorageGET("default_discount_price_type")? this.vhAuth.localStorageGET("default_discount_price_type").value : '%';
  symbol = JSON.parse(localStorage.getItem('vhsales_currencyFormat')).symbol.text;
  show_discount_product = this.vhAuth.localStorageGET("show_discount_product");
  list_product =[
    {
      name: 'My in m vch XPrinter H500',
      price :4000000,
      quantity: 1,
      unit:'ci'
    },
    {
      name: 'My in ha n XPrinter XP-470B',
      price :599000,
      quantity: 1,
      unit:'ci'

    },
    {
      name: 'My POS bn hng Sunmi T2 (1 mn hnh) km my k80',
      price :10400000,
      quantity: 1,
      unit:'ci'

    },
    {
      name: 'My POS 2 mn hnh T350 j1900 ram 4gb ssd 128 (15x12)',
      price :11199000,
      quantity: 1,
      unit:'ci'

    },
    {
      name: 'My qut m vch OPTICON OPR-3201',
      price :11199000,
      quantity: 1,
      unit:'ci'

    }
  ]
  showPortal = false;
  @Input() showConfigSales = false;
  @Output() hide = new EventEmitter<boolean>()
  @Output() openSubScreen = new EventEmitter<boolean>()
  constructor(
    public vhAuth: VhAuth,
    private vhQuerySales: VhQuerySales,
    private vhImage: VhImage,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhAlgorithm:VhAlgorithm
  ) {  
  }

  ngOnInit(): void {
    
    
    if(typeof(this.tax) == 'undefined'){
      this.vhAuth.localStorageSET("show_best_selling", true)
      this.vhAuth.localStorageSET("show_tax", true)
      this.vhAuth.localStorageSET("show_fee", true)
      this.vhAuth.localStorageSET("show_discount", true)
      this.vhAuth.localStorageSET("show_note", true);
      this.vhAuth.localStorageSET("show_cash", true);
      this.vhAuth.localStorageSET("show_rest", true);
    }   
  }


  updateSetting(title, ev) {
    this.vhAuth.localStorageSET(title, ev)
  }

  getFile() {
    document.getElementById("file-upload").click();
  }

  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "sales_template_sub_pos2", this.image_template_sub_pos2 || '').then(
      photoURL => {
        this.image_template_sub_pos2 = photoURL;
        // this.vhQuerySales.updateAds(this.image_template_sub_pos2._id, { url: photoURL })
        this.updateSetting('image_template_sub_pos2', { url: photoURL })
        this.vhComponent.alertMessageDesktop('success', (this.languageService.translate("Image was loaded successfully")));
      },
      () => {
        this.vhComponent.alertMessageDesktop('error', (this.languageService.translate("Image was loaded successfully")));
      }
    );
  }
}
